
.projects_section{
    /* height: 100vh; */
    height: auto;
    width: 90vw;
    margin: auto;
    /* border: 1px solid red; */
    box-sizing: border-box;
  }

.projects_container{
    margin-top: 56px;
    /* height: 90vh; */
    height: auto;
    width: 100%;
    border: 1px solid var(--light-blue);
    padding: 10px;
    box-sizing: border-box;
}

.all_projects_holder{
  height: 100%;
  width: 100%; 
}

.single_project_container{
  width: 100%;
  display: flex;
  height: auto;
  gap: 5px;
  /* margin-bottom: 5px; */
  border-radius: 5px;
  padding: 10px;
  margin-top: 10px;
  box-shadow: rgba(82, 121, 128, 0.767) 0px 6px 24px 5px, rgba(115, 218, 158, 0.247) 0px 0px 0px 1px;
}

.single_project_container a{
  background-color: var(--dark-blue);
  color: var(--light-blue);
  padding: 10px;
  padding-top: 0px;
  padding-bottom: 0px;
  border-radius: 5px;
  margin: 5px;
  margin-left: 0px;
}


.project_image{
  width: 50%;
  object-fit: cover;
  /* border: 1px solid red; */
  border-radius: 5px;
  overflow: hidden;
}

.project_details{
  width: 50%;
  /* border: 1px solid red; */
  background-color: var(--purple);
  background-color: var(--light-blue);
  text-align: left;
  padding: 10px;
  color: var(--dark-blue);
  border-radius: 5px;
  /* font-size: 10px; */
}


ul{
  list-style-type: disc;
  margin-left: 0%;
  padding-left: 0px;
  font-size: 13px;
}

ul > ul > li{
  list-style-type: disc;
  margin-left: 5%;
  /* padding-left: 0px; */
}

.project_image img{
  width: 100%;
  height: 100%;
}

.childUl{
  list-style-type: circle;
  padding-left: 0%;
}

.mainUl>li{
  /* padding-left: 4%; */
  font-weight: 700;
}



@media screen and (min-width:400px) and (max-width:800px) {

  .single_project_container{
    width: 100%;
    display: flex;
    flex-direction: column;
    height: auto;
  }

  .project_image{
    width: 100%;
    object-fit: cover;
  }
  
  .project_details{
    width: 100%;
  }
}

@media screen and (min-width:200px) and (max-width:399px) {

  .single_project_container{
    width: 100%;
    display: flex;
    flex-direction: column;
    font-size: 15px;
    height: auto;
  }

  .project_image{
    width: 100%;
    object-fit: cover;
  }
  
  .project_details{
    width: 100%;
    font-size: 7px;
  }

  ul{
    font-size: 7px;
  }

  .project_details h2,h4{
    font-size: 10px;
  }

}

