
.contact_section{
    height: 90vh;
    width: 100vw;
    /* border: 1px solid red; */
    box-sizing: border-box;
  }


.contact_container{
    height: 90vh;
    margin-top: 56px;
    margin-bottom: 0px;
    /* border: 1px solid green; */
    flex-direction: column;
}


.contact_text{
  text-align:center;
  height:50%;
  justify-content:center;
  align-items:center;
  font-size:30px;
}

.contact_achor_icons{
  background-color:#B6EADA;
  height:100px;
  margin-right: 5px;
  width:100px;
  padding:20px;
  font-size:55px;
  border-radius:5px;
}


/* medium screen */
@media screen and (min-width:400px) and (max-width:600px) {
  .contact_text{
    font-size: 15px;
  }  

  .contact_text h1{
    font-size: 35px;
  }

  .contact_achor_icons{
    height:50px;
    width:50px;
    padding:10px;
    font-size:20px;
  }
}



/* small screen */
@media screen and (min-width:200px) and (max-width:399px) {
  .contact_text{
    font-size: 15px;
  }  

  .contact_text h1{
    font-size: 18px;
  }

  .contact_achor_icons{
    height:50px;
    width:50px;
    padding:10px;
    font-size:20px;
  }
}
